html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font: 0.75em sans-serif;
}

/*****Media Query*****/

@media screen and (min-width: 600px) {
  body {
    font-size: 1em;
  }
}

@media screen and (min-width: 950px) {
  body {
    max-width: 1200px;
    margin: auto;
  }
}



